import React, { useState, useEffect, useRef } from "react";
import { useHistory } from "react-router-dom";

import RatingScale from "../components/RatingScale";

export default function PostRating() {
  const [arousal, setArousal] = useState(0);
  const [dominance, setDominance] = useState(0);
  const [valence, setValence] = useState(0);
  const [finished, setFinished] = useState(false);
  const [error, setError] = useState("");
  let history = useHistory();
  const timerID = useRef();

  useEffect(() => {
    const submitRating = async () => {
      if (finished) {
        setError("");
        if (timerID.current) {
          clearTimeout(timerID.current);
        }
        history.push("/end");
      }
    };

    submitRating();
  }, [finished, arousal, dominance, valence, history]);

  useEffect(() => {
    timerID.current = setTimeout(() => {
      setFinished(true);
    }, 10000);
  }, []);

  useEffect(() => {
    if (!(arousal === 0 || dominance === 0 || valence === 0)) {
      console.log(
        `DEBUG: arousal: ${arousal}, dominance: ${dominance}, valence: ${valence}`
      );
      setFinished(true);
    }
  }, [arousal, dominance, valence]);

  return (
    <div>
      <span className="block font-medium tracking-wide text-red-500 text-base mt-1 text-center mb-3">
        {error}
      </span>
      <RatingScale
        ratingTitle="Arousal (ìê·¹ì ì„)"
        leftText="â† ì•ˆì •ì "
        rightText="ìê·¹ì  â†’"
        setRating={setArousal}
      />
      <div className="mt-8">
        <RatingScale
          ratingTitle="Dominance (ì§€ë°°ì ì„)"
          leftText="â† ì§€ë°°ë˜ëŠ”"
          rightText="ì§€ë°°í•˜ëŠ” â†’"
          setRating={setDominance}
        />
      </div>
      <div className="mt-8">
        <RatingScale
          ratingTitle="Valence (ê°ì •ê°€)"
          leftText="â† ë¶ˆì¾Œí•œ"
          rightText="ìœ ì¾Œí•œ â†’"
          setRating={setValence}
        />
      </div>
    </div>
  );
}
